
body {
    background-color: #ABC4AA;
}

h1{
    font-family:'Noto Serif';
    font-size: 3.125rem;
    font-weight: bold;
}
h2 {
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.875rem;
}

p, a, .navbar-nav a{
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.125rem;   
    font-weight: bold; 
}
h4 {
    font-family: 'Ubuntu', sans-serif;
    font-size: 1.375rem;
    font-weight: bold;
}

a{
    text-decoration: none;
    color: black;
}
.common-section {
    padding: 7% 15%;
}

.navbar-brand{
    font-family: 'Calligraffitti', cursive;
    font-size: 25px;
    font-weight: bolder;
}

.common-section a:hover{
    color: #F3DEBA;
}
/*nav{
    margin: 0 50%;
}*/

/* Welcome page */
nav a{
    margin: 0 2rem;
}

.img-welcome{
    height: 50rem;
    width: 100%;
}


.word-welcome{
    position: absolute;
    background-color: #ABC4AA;
    margin-left: 40%;
    margin-top: -35rem;
    padding: 5%;
}


/* Tabs sections */

.img-illustration{
    height: 10rem;
    width: 10rem;
    justify-self: center;
    margin: 13%;
}

.title-section{
    display: grid;
    justify-content: center;
    align-items: center;
    /*margin: 7% 0;*/
    margin-bottom: 7%;
}


.button-wrap {
    position: relative;
    /*width: 40%;*/
    width: 7.70rem;
    height: 2.063rem;
    display: flex;
    background-color: #A9907E;
    justify-content: center;
    border-radius: 50px;
    margin-left: 3.5rem;
}

.button-wrap .text-button{
    margin-top: 3px;
}

.tab-card{
    padding: 0;
    margin: 2.5rem;
    height: 20.688rem;
    width: 18.875rem;
    background-color: #F3DEBA;
    box-shadow: 5px 5px 5px grey;
}

.row {
    justify-content: center;
}

.tab-card img{
    height: 13.938rem;
    width: 100%;
}

.card-info{
    margin: 5%;
}
.tab-card .card-info div{
   display: flex;
}

/* Testimonial */

#testimonials, #about-us, .footer {
    background-color: #675D50;
}

.testi-card{
    display: grid;
    justify-content: center;
    margin: -8% 0;
    /*background-color: white;*/
    margin-bottom: 5%;
    /*padding: auto;*/
}

.testi-card p{
    background-color: #F3DEBA;
    padding: 7rem 5rem 5rem 5rem;
    /*height: 17rem;*/
    width: 25rem;
    border-radius: 1.25rem; 
}

.img-user{
    position: relative;
    z-index: 1;
    bottom: -12rem;
    justify-self: center;
}

.img-com{
    position: relative;
    justify-self: right;
    z-index: 1;
    bottom: -2rem;
    left: 4rem;
}

/* Let command-section */

/*.command-form input, .command-form select{
    background-color: #F3DEBA; 
}*/

/* About us section*/
.p1, .p2 {
    background-color: #ABC4AA;
    padding: 5%;
    width: 50%/*34rem;*/;
}

.p2{
    margin-left: 50%;
}

/* contact section */
.contacts-infos {
    display: grid;
    justify-content: center;
    
    
}

.contacts-infos .map, .infos{
    margin: auto;
}

/* responsive */
@media screen and (max-width: 980px){
    .common-section {
        padding: 7% 5%;
    };

    .p1, .p2 {
        background-color: #ABC4AA;
        padding: 5%;
        width: 50%/*34rem;*/;
    }
    
    .p2{
        margin-left: 50%;
    }

    h4{
        margin-top: 30px;
    }
}